 *
 * Replace them with your own styles or remove the file completely.
 */

.atc-select-wrapper {
	padding: 20px;
	background: #0f141f;
	.atc-select-image {
		img {
			width: 100%;
		}
	}
	.atc-select-title {
		h5 {
			font-size: 32px;
			color: #fafcff;
			text-transform: uppercase;
			text-align: center;
			line-height: 140%;
			font-weight: bold;
			margin-top: 20px;
		}
		h3 {
			font-size: 18px;
			line-height: 140%;
			text-align: center;
			font-weight: normal;
		}
	}
	.atc-select-content {
		li {
			font-size: 16px;
			color: #fafcff;
			line-height: 141%;
			margin-bottom: 16px;
			list-style-type: none;
			&:before {
				font-family: "FontAwesome";
				content: "\f005";
				color: white;
				margin-right: 15px;
				display: inline-block;
			}
		}
	}
	.atc-select-button {
		margin-bottom: 16px;
		width: 100%;
		.atc-select-btn {
			border: 1px solid #ff3514;
			width: 100%;
			text-align: center;
			font-size: 18px;
			text-transform: uppercase;
			line-height: 22px;
			display: block;
			padding: 20px 0px;
			color: #ff3514;
			text-decoration: none;
			&:hover {
				background: #ff3514;
				color: white;
			}
		}
	}
}
